import { Component, h, Listen, Prop } from '@stencil/core';

@Component({
	tag: 'ks-product-tooltip',
	styleUrl: 'product-tooltip.css'
})
export class ProductButton {
    @Prop() message: string;
    @Prop({mutable: true, reflect: true}) active: boolean = false;

    @Listen("mouseover") onMouseover() {
        this.active = true;
    }
    @Listen("mouseout") onMouseout() {
        this.active = false;
    }

	render() {
		return [
            <slot/>,
            <div class="arrow" />,
            <div class="message">{this.message}</div>
        ];
	}
}