import { Component, h, Prop, Element } from '@stencil/core';
@Component({
    tag: 'ks-product-tab',
    styleUrl: 'product-tab.css'
})
export class ProductTab {
    @Element() root: HTMLKsProductTabElement;

    @Prop({mutable: true, reflect: true}) main: boolean;
    @Prop({mutable: true, reflect: true}) open: boolean;
    @Prop() name: string;
    @Prop() content: string;

	render() {
        return [
			<button class="accordion" onClick={()=>this.onOpen()}>
                {this.name}
                <ks-icon name={this.open ? "minus" : "plus"}></ks-icon>
            </button>,
            <div class="tab-content" innerHTML={this.content}></div>
        ];
    }

    onOpen() {
        this.open = !this.open;
        if(this.open) {
            const tabs = Array.from(this.root.parentElement.children) as Array<HTMLKsProductTabElement>;
            const index = tabs.indexOf(this.root);

            tabs.forEach(element => element.removeAttribute("main"));
            this.main = true;

            (this.root.closest('ks-product-tabs') as any).active = index;
        }
    }
    
    
}
